/*--------------------adjustment----------------------*/
*,
*::before,
*::after{
	box-sizing: border-box;
}
html{
	font-size: ($rootFontSize / 16px) * 100%;
}
html, body{
	margin: 0;
	padding: 0;
}
body{
	font-family:'Plus Jakarta Sans', sans-serif;
	font-weight: normal;
	font-size: rem($baseFontSize);
	min-width: 320px;
	position: relative;
	// line-height: 1.6;
	-webkit-font-smoothing: antialiased;
	overflow-x: hidden;
	color: $text;

	.lock & {
		overflow: hidden;
		touch-action: none;
		overflow-behavior: none;
	}
}

