// Прижать футер к низу

html, body{
	height: 100%;
}
.main{
	display: flex;
	flex-direction: column;
	min-height: 100%;
	overflow: hidden;

	@supports(overflow: clip){
		overflow: clip;
	}

	& > *{
		min-width: 0;
	}

	.header{
		flex: 0 0 auto;
	}
	.main-content{
		flex: 1 1 auto;
	}
	.footer{
		flex: 0 0 auto;
		// margin-top: auto;
	}
}

