h1,h2,h3,h4,h5,h6{
	font-weight: inherit;
	font-size: inherit;
}
input
input[type="text"],
input[type="email"],
input[type="tel"],
textarea{
	appearance: none;
}
.input,
input{
	outline: none;
	display: block;
	width: 100%;
}
textarea.input{
	resize: none;
	padding: 0 0;
}
